          align-items: center;
  color: white;
  background-color: #1e1e1e;
  -webkit-justify-content: center;
          justify-content: center;
}
.card_listitem {
  padding: 10px 10px;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  gap: 10px;
  background-position: center;
  transition: background 0.8s;
  color: white;
  background-color: #1e1e1e;
}

.card_listitem:hover {
  background: #1e1e1e radial-gradient(circle, transparent 1%, #111111 1%)
    center/15000%;
}
.card_listitem:active {
  background-size: 100%;
  transition: background 0s;
}
a {
  text-decoration: none;
  color: #b3d4fc;
  background-color: #1e1e1e;
}
.mascot {
  width: 100%;
